import { Box, Text, Tooltip } from "@chakra-ui/react";
import { Link } from "gatsby";

interface PortfolioProps {
  fontSize?: number | string; // fontSize prop 
  dateText?: string; //   prop 
}

const Portfolio = ({ fontSize = 14, dateText }: PortfolioProps) => {
  const currentMonth = new Date().getMonth() + 1; //  
  const defaultDateText = `${currentMonth}  ...`; //   

  return (
    <Link to="/portfolio">
      <Box pos="relative">
        <Text
          pos="absolute"
          fontStyle="italic"
          whiteSpace="nowrap"
          top="-10px"
          left="-10px"
          fontSize={10}
          fontWeight={800}
        >
          {dateText || defaultDateText} {/* prop    */}
        </Text>
        <Tooltip label="Coming Soon!">
          <Text
            as="del"
            fontSize={fontSize} //  fontSize 
            fontStyle="italic"
            fontWeight={800}
            padding={1}
            _active={{ bg: "transparent" }}
            _hover={{
              textDecoration: "underline",
            }}
          >
            Portfolio
          </Text>
        </Tooltip>
      </Box>
    </Link>
  );
};

export default Portfolio;